import datas from '../../common/js/datas.js'
export default {
	data() {
		return {
			userArr: []
		};
	},
	methods: {
		//获取关键词
		search(e) {
			this.userArr = []
			let searchval = e.detail.value;
			if (e.detail.value.length > 1) {
				this.searchUser(e.detail.value)
			}
		},
		//寻找关联词匹配的好友
		searchUser(e) {
			let arr = datas.friends();
			let exp = eval("/" + e + "/g" );
			for (let i = 0; i < arr.length; i++) {
				if (arr[i].name.search(e) != -1 || arr[i].email.search(e) != -1) {
					this.isFriend(arr[i]);
					arr[i].name = arr[i].name.replace(exp, "<span style='color:#4AAAFF;'>"+ e + "</span>");
					arr[i].email = arr[i].email.replace(exp, "<span style='color:#4AAAFF;'>"+ e + "</span>");
					this.userArr.push(arr[i]);
				}
			}
			console.log(this.userArr)
		},
		//判断是否为好友
		isFriend(e) {
			let tip = 0;
			let arr = datas.isFriend();
			for(let i = 0; i < arr.length; i++) {
				if (arr[i].friend == e.id) {
					tip = 1;
				}
			}
			e.tip = tip;
		},
		//取消
		navBack() {
			uni.navigateBack({})
		},
		//去用户详情页
		goUserDetail(id) {
			console.log(id)
			uni.navigateTo({
				url: '../userdetail/userdetail?id='+id
			})
		}
	}
}
